.movies__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Georgia, "Times New Roman", Times, serif;
}

.movies__movie {
  width: 80%;
  display: flex;
  align-items: center;
  padding: 20px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-top: none;
  border-radius: 15px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  background-color: white;
}

.movies__movie img {
  width: 200px;
  height: 260px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 15px;
}

.movie__data {
  padding: 0px 15px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.movie__title {
  font-weight: 900px;
  font-size: 30px;
  margin-top: 70px;
  padding: 20px;
  background: linear-gradient(#666666, #1a1a1a);
  width: 80%;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.movie__title h3 {
  margin: 0px;
  color: white;
}

.movie__year {
  font-size: 20px;
  font-weight: 500;
  margin: 0px;
  color: white;
  margin-left: 10px;
}

.genres {
  display: flex;
  justify-content: space-evenly;
  font-weight: 500;
  list-style: none;
  padding-left: 0px;
  align-items: center;
}

.genres span {
  font-weight: 800;
  font-size: 20px;
  padding-right: 5px;
}

.genres__genre {
  padding-right: 5px;
  font-size: 17px;
}

@media screen and (max-width: 700px) {
  .movies__movie {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
